___CSS_LOADER_EXPORT___.push([module.id, `:root {
	--bg-color-primary: #10b981;
	--bg-color-secondary: #047857;
	--bg-color-highlight: #6ee7b7;
	--text-color-primary: #000000;
	--text-color-secondary: #ffffff;
	--text-color-highlight: #000000;
	--border-color: #000000;
}

body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	margin: 0;
	padding: 0;
	align-items: center;
	background-color: var(--bg-color-primary);
	color: var(--text-color-primary);
	font-family: sans-serif;
}

* {
	box-sizing: border-box;
}

a {
	color: inherit;
}

a:hover {
	color: #5b54c9;
}

header {
	width: 100%;
	padding: 20px;
	background-color: var(--bg-color-secondary);
	color: var(--text-color-secondary);
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid var(--border-color);
}

nav {
	display: flex;
	justify-content: space-around;
	width: 80%;
}

button {
	height: 50px;
	width: 100px;
	padding: 10px 30px;
	border-radius: 5px;
	background-color: var(--bg-color-highlight);
	color: var(--text-color-highlight);
	border: 1px solid var(--border-color);
	font-weight: 700;
}

button:hover {
	text-decoration: underline;
	cursor: pointer;
}

#darkmodebtn {
	padding: 10px;
	width: 50px;
	height: 50px;
	border-radius: 25px;
	font-weight: 300;
}

#darkmodebtn:hover {
	background-color: var(--bg-color-primary);
}

#content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#headline {
	background-color: var(--bg-color-secondary);
	color: var(--text-color-secondary);
	width: 100%;
	padding: 20px 50px;
	margin: 50px 0;
	text-align: center;
	border: 1px solid var(--border-color);
}

.container {
	display: grid;
	grid-template-columns: 3fr 2fr;
	grid-template-rows: 1fr 5fr;
	width: 80%;
	min-height: 400px;
	height: min(600px, 70vh);
	padding: 50px;
	background-color: var(--bg-color-secondary);
	color: var(--text-color-primary);
	border: 1px solid var(--border-color);
}

#containerheading {
	grid-row: 1 / 2;
	grid-column: 1 / 3;
	border: 1px solid var(--border-color);
	background-color: var(--bg-color-primary);
	text-align: center;
}

.col {
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid var(--border-color);
	background-color: var(--bg-color-primary);
	padding: 5px;
	grid-row: 2 / 3;
}

#col1 {
	border-right: none;
}

#col2 {
	border-left: none;
}

#col1 > img {
	border: 1px solid var(--border-color);
}

img {
	max-width: 100%;
	max-height: 100%;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;CACC,2BAA2B;CAC3B,6BAA6B;CAC7B,6BAA6B;CAC7B,6BAA6B;CAC7B,+BAA+B;CAC/B,+BAA+B;CAC/B,uBAAuB;AACxB;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,iBAAiB;CACjB,SAAS;CACT,UAAU;CACV,mBAAmB;CACnB,yCAAyC;CACzC,gCAAgC;CAChC,uBAAuB;AACxB;;AAEA;CACC,sBAAsB;AACvB;;AAEA;CACC,cAAc;AACf;;AAEA;CACC,cAAc;AACf;;AAEA;CACC,WAAW;CACX,aAAa;CACb,2CAA2C;CAC3C,kCAAkC;CAClC,aAAa;CACb,8BAA8B;CAC9B,4CAA4C;AAC7C;;AAEA;CACC,aAAa;CACb,6BAA6B;CAC7B,UAAU;AACX;;AAEA;CACC,YAAY;CACZ,YAAY;CACZ,kBAAkB;CAClB,kBAAkB;CAClB,2CAA2C;CAC3C,kCAAkC;CAClC,qCAAqC;CACrC,gBAAgB;AACjB;;AAEA;CACC,0BAA0B;CAC1B,eAAe;AAChB;;AAEA;CACC,aAAa;CACb,WAAW;CACX,YAAY;CACZ,mBAAmB;CACnB,gBAAgB;AACjB;;AAEA;CACC,yCAAyC;AAC1C;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,uBAAuB;CACvB,mBAAmB;AACpB;;AAEA;CACC,2CAA2C;CAC3C,kCAAkC;CAClC,WAAW;CACX,kBAAkB;CAClB,cAAc;CACd,kBAAkB;CAClB,qCAAqC;AACtC;;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,2BAA2B;CAC3B,UAAU;CACV,iBAAiB;CACjB,wBAAwB;CACxB,aAAa;CACb,2CAA2C;CAC3C,gCAAgC;CAChC,qCAAqC;AACtC;;AAEA;CACC,eAAe;CACf,kBAAkB;CAClB,qCAAqC;CACrC,yCAAyC;CACzC,kBAAkB;AACnB;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,mBAAmB;CACnB,qCAAqC;CACrC,yCAAyC;CACzC,YAAY;CACZ,eAAe;AAChB;;AAEA;CACC,kBAAkB;AACnB;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,qCAAqC;AACtC;;AAEA;CACC,eAAe;CACf,gBAAgB;AACjB","sourcesContent":[":root {\n\t--bg-color-primary: #10b981;\n\t--bg-color-secondary: #047857;\n\t--bg-color-highlight: #6ee7b7;\n\t--text-color-primary: #000000;\n\t--text-color-secondary: #ffffff;\n\t--text-color-highlight: #000000;\n\t--border-color: #000000;\n}\n\nbody {\n\tdisplay: flex;\n\tflex-direction: column;\n\tmin-height: 100vh;\n\tmargin: 0;\n\tpadding: 0;\n\talign-items: center;\n\tbackground-color: var(--bg-color-primary);\n\tcolor: var(--text-color-primary);\n\tfont-family: sans-serif;\n}\n\n* {\n\tbox-sizing: border-box;\n}\n\na {\n\tcolor: inherit;\n}\n\na:hover {\n\tcolor: #5b54c9;\n}\n\nheader {\n\twidth: 100%;\n\tpadding: 20px;\n\tbackground-color: var(--bg-color-second